<template>
  <div>
    <el-menu :default-active="activeIndex" class="el-menu-demo" mode="horizontal" @select="handleSelect">
      <el-menu-item index="home"></el-menu-item>
      <el-menu-item index="project"></el-menu-item>
      <el-menu-item index="app"></el-menu-item>
      <!-- <el-menu-item index="abrowse"></el-menu-item>
      <el-menu-item index="about"></el-menu-item> -->
    </el-menu>
    <div class="userDiv" v-show="$store.state.username">
      <!-- <img src="../assets/img/avatar.png" alt=""> -->
      <span class="nameStyle">{{$store.state.username}}</span>
      <el-button type="text" @click="logout"></el-button>
    </div>

  </div>
</template>

<script>
import bus from '../bus.js'
export default {
  data () {
    return {
      activeIndex: '',
    }
  },
  components: {
  },
  mounted () {
    if (!sessionStorage.navbarItem) {
      this.activeIndex = 'home'
    } else {
      this.activeIndex = sessionStorage.navbarItem
    }
    bus.$on("handleSelect", (key) => {
      this.handleSelect(key)
    })
  },
  methods: {
    handleSelect(key, keyPath) {
      let self = this
      switch (key) {
        case 'home':
          this.activeIndex = 'home'
          sessionStorage.setItem('navbarItem', 'home')
          this.$router.push({'path': '/'})
          break
        case 'project':
          this.activeIndex = 'project'
          sessionStorage.setItem('navbarItem', 'project')
          this.$router.push({'name': 'project_list'})
          break
        case 'app':
          this.activeIndex = 'app'
          sessionStorage.setItem('navbarItem', 'app')
          this.$router.push({'name': 'app'})
          break
        case 'abrowse':
          sessionStorage.setItem('navbarItem', 'about')
          this.$router.push({'name': 'about'})
          break
        case 'about':
          sessionStorage.setItem('navbarItem', 'about')
          this.$router.push({'name': 'about'})
          break
      }
    },
    logout () {
      this.$confirm('?', '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning',
          center: true
        }).then(() => {
          sessionStorage.removeItem('navbarItem')
          window.location.href = 'https://colorseq.com/logout'
        }).catch(() => {});
    },
  }
}
</script>

<style scoped="true">
.userDiv {
  position: absolute;
  top: 18px;
  right: 50px;
}
.nameStyle {
  margin-right: 30px;
  color: #b5a199;
}
.text-center {
  text-align: center;
}
</style>



